import React, {useCallback, useContext, useEffect, useMemo, useState} from "react";
import styled from "styled-components";
import {getBalanceNumber, getNumber} from "../../../utils/formatBalance";
import useApprove from "../../../hooks/useApprove";
import useStake from "../../../hooks/useStake";
import {FarmContext} from "../../../contexts/farm_provider/farm_provider";
import useModal from "../../../hooks/useModal";
import {DepositModal} from "../../Farm/components/DepositModal";
import Button from "../../../components/Button";
import Value from "../../../components/Value";
import {Farm} from "../../../contexts/Farms/types";
import {LoopHook} from "../../../hooks/loop_hook";
import {UserAccoutContext} from "../../../contexts/farm_provider/user_account_provider";
import {Context as FarmsContext} from "../../../contexts/Farms";
import BigNumber from "bignumber.js";
import {useWallet} from "use-wallet";


export const FarmCardChild: React.FC = () => {

    const [requestedApproval, setRequestedApproval] = useState(false);

    const farm: Farm = useContext(FarmContext);
    const {setUnharvested,unharvested} = useContext(FarmsContext);
    const {allPremium,ethAmout} = useContext(UserAccoutContext);



    const {onApprove} = useApprove();// 申请受权

    const {onStake} = useStake(); //  stake Fn


    const {tokenAmout} = useContext(UserAccoutContext);
    const {allowance, tokenBalance, totalSupply, isPause,premium} = LoopHook(farm);
    const {allReward,} = useContext(FarmsContext);

    const tokenName = farm.name;


    const [onPresentDeposit] = useModal(
        <DepositModal
            staked={tokenBalance}
            getTokenAmout={tokenAmout}
            onConfirm={onStake}
            tokenName={tokenName}
        />,
    );


    const handleApprove = useCallback(async () => {
        try {
            setRequestedApproval(true);
            const txHash = await onApprove();
            // user rejected tx or didn't go thru
            if (!txHash) {
                setRequestedApproval(false)
            }
        } catch (e) {
        }
    }, [onApprove, setRequestedApproval]);


    useEffect(() => {
        if(tokenBalance){
            setUnharvested(unharvested+1);
        }


    },[tokenBalance]);








    return (
        <StyledFarmCardsChild>
            <StyledFarmCardsUl>
                <li><span/>Simulated TSI:<Flex><Value value={farm.tsl}/></Flex></li>
                <li><span/>Premium:<Flex><Value value={premium || 0}/></Flex></li>
                <li><span/>Staking Pool:<Flex><Value value={getBalanceNumber(totalSupply.times(4))}/></Flex></li>
                <li><span/>Estimated APY: <Flex>{!allPremium ? '--' :(new BigNumber(allReward).times(premium).div(allPremium).div(totalSupply).toNumber() * 100 / 14 * 365).toFixed(2) +'%'}</Flex></li>
                {/*<li><span/>totalSupply <Flex><Value value={getBalanceNumber(totalSupply)}/></Flex></li>*/}
                {/*<li><span/>Estimated APY:*/}
                {/*<Flex>*/}
                {/*{apy ? `${apy.toNumber().toFixed(2)}%` : 'Loading ...'}*/}
                {/*</Flex>*/}
                {/*</li>*/}

                {/*<li><span/>Lockup period:<Flex>14 Days</Flex></li>*/}
                {/*<li><span/>Additional rewards: <Flex>xxx</Flex></li>*/}

            </StyledFarmCardsUl>
            <BottomWidget>
                {allowance <= 0 ? (
                    <Button
                        disabled={requestedApproval ||  ethAmout.eq(0) || isPause }
                        onClick={handleApprove}
                        text={`Approve`}
                    />
                ) : (
                    <>
                        <Button
                            disabled={isPause || tokenAmout.eq(0) || ethAmout.eq(0) }
                            text="Become a writer"
                            onClick={onPresentDeposit}

                        />
                    </>
                )}

            </BottomWidget>
        </StyledFarmCardsChild>
    )
};





const Flex = styled.div`
flex:1;
text-align: right;
font-weight: bold;
font-size: 12px;
color: #FFFFFF;


`;
const StyledFarmCardsChild = styled.div`
width:100%;
box-sizing: border-box;

`;
const StyledFarmCardsUl = styled.ul`
  align-items: center;
  justify-content: center;
  padding:0;
  > li{
  height:30px;
  display:flex;
  align-items:center;
  text-transform:capitalize;
color: rgba(171, 189, 236, 1);
font-size: 12px;
font-weight: 400;
color: #ABBDEC;
line-height: 30px;

>span{
    width:6px;
    height:6px;
    background-color:rgba(171, 189, 236, 1);
    border-radius:100px;
    margin-right:10px;
}

  
  }
`;

const BottomWidget = styled.div`
width:92%;
margin:20px auto;
`;
