import React from 'react'
import styled from 'styled-components'

export interface InputProps {
    endAdornment?: React.ReactNode,
    onChange: (e: React.FormEvent<HTMLInputElement>) => void,
    placeholder?: string,
    startAdornment?: React.ReactNode,
    value: string,
}

const Input: React.FC<InputProps> = ({
                                         endAdornment,
                                         onChange,
                                         placeholder,
                                         startAdornment,
                                         value,
                                     }) => {
    return (
        <StyledInputWrapper>
            {!!startAdornment && startAdornment}
            <StyledInput placeholder={placeholder} value={value} onChange={onChange}/>
            {!!endAdornment && endAdornment}
        </StyledInputWrapper>
    )
};

const StyledInputWrapper = styled.div`
  align-items: center;
  background-color: rgba(30, 45, 86, 1);
  border-radius: 5px;
  border: 1px solid rgba(39, 55, 97, 1);
  //box-shadow: inset 4px 4px 8px ${props => props.theme.color.grey[300]},inset -6px -6px 12px ${props => props.theme.color.grey[100]};
  display: flex;
  height: 50px;
  padding: 0 ${props => props.theme.spacing[3]}px;
`;

const StyledInput = styled.input`
  background: none;
  border: 0;
  color: ${props => props.theme.color.whites[1001]};
  font-size: 18px;
  flex: 1;
  height: 56px;
  margin: 0;
  padding: 0;
  outline: none;
  @media (max-width: 768px) {
  width: 35%;
  }
`;

export default Input
