import {useCallback, useContext} from 'react'


import {useWallet} from 'use-wallet'

import BigNumber from "bignumber.js";
import {FarmContext} from "../contexts/farm_provider/farm_provider";
import {UserAccoutContext} from "../contexts/farm_provider/user_account_provider";
import {getBalanceNumber} from "../utils/formatBalance";

const useStake = () => {
    const {sC} = useContext(FarmContext);
    const {account} = useWallet();

    const handleStake = useCallback(
        async (amount: string,tokenAmout:BigNumber) => {
            console.log( new BigNumber(amount).div(4).times(new BigNumber(10).pow(18)).toString());
            if (tokenAmout.times(4).comparedTo(new BigNumber(amount).times(1e18)) < 0 ){
                amount = tokenAmout.toString();
                 // await onConfirm(getBalanceNumber(getTokenAmout.times(4)).toString());
            }else{
                amount =  new BigNumber(amount).div(4).times(new BigNumber(10).pow(18)).toString();
            }
            console.log(amount,tokenAmout.toString(),getBalanceNumber(tokenAmout));
            const txHash = await sC.methods
                .stake(amount)
                .send({from:account})
                .on('transactionHash', (tx: any) => {
                    console.log(tx);
                    return tx.transactionHash
                });
        },
        [account, sC],
    );

    return {onStake: handleStake}
};

export default useStake

