import React, {useCallback, useContext, useEffect, useMemo, useState} from 'react'
import styled, {keyframes} from 'styled-components'
import Card from '../../../components/Card'
import CardContent from '../../../components/CardContent'
import CardIcon from '../../../components/CardIcon'
import {FarmProvider} from "../../../contexts/farm_provider/farm_provider";
import {FarmCardChild} from "./farm_cards_child";
import {Farm} from "../../../contexts/Farms/types";
import Spacer from "../../../components/Spacer";
import {Context as FarmsContext} from "../../../contexts/Farms";
import {UserAccoutProvider} from "../../../contexts/farm_provider/user_account_provider";
import {TestWidget} from "./tests";
import {TopTitleWidgets} from "./top_title";
import ImageNameSvg from "../../../assets/img/jiantou.svg"

const FarmCards: React.FC = () => {
    const {farms} = useContext(FarmsContext);
    const [slideIndex, setSlideIndex] = useState(0);
    return (
        <UserAccoutProvider farms={farms}>
            <StyledCards>
                <TopTitleWidgets/>
                <Spacer/>
                <Card color="rgba(23, 37, 74, 1)">
                    <StyleTopTitle>
                        Become an Underwriter
                        <div>
                            <p onClick={() => {
                                if (slideIndex > 0) {
                                    setSlideIndex(slideIndex - 1)
                                }
                            }}><img src={ImageNameSvg} alt="" style={{transform: "rotate(-180deg)"}}/></p>
                            <p onClick={() => {
                                if (slideIndex < farms.length - 4) {
                                    setSlideIndex(slideIndex + 1)
                                }
                            }}><img src={ImageNameSvg} alt=""/></p>
                        </div>
                    </StyleTopTitle>

                    <div style={{padding: "20px", boxSizing: "border-box"}}>
                        <TestWidget slideIndex={slideIndex} setSlideIndex={setSlideIndex}>
                            {farms.map((farm, j) => (
                                <React.Fragment key={j}>
                                    <FarmCard farm={farms[j]}/>
                                </React.Fragment>
                            ))}
                        </TestWidget>
                    </div>
                </Card>


            </StyledCards>
        </UserAccoutProvider>
    )
};

export interface FarmCardProps {
    farm: Farm
}

const FarmCard: React.FC<FarmCardProps> = ({farm}) => {

    return (
        <StyledCardWrapper>
            <Card>
                <CardContent>
                    <StyledContent>
                        <CardIcon size={40}><img src={farm.icon} alt=""/></CardIcon>
                        <StyledTitle>{farm.name}</StyledTitle>
                        <FarmProvider model={farm}>
                            <FarmCardChild/>
                        </FarmProvider>
                    </StyledContent>
                </CardContent>
            </Card>
        </StyledCardWrapper>
    )
};


export const StyleTopTitle = styled.h2`
height: 50px;
padding:0 30px;
box-sizing: border-box;
background: #17254A;
border-bottom: 1px solid #273761;
box-shadow: 0 0 95px 0 rgba(45, 70, 146, 0.13);
border-radius: 4px 4px 0 0;
font-size: 20px;
font-weight: 400;
color: #FFFFFF;
line-height: 50px;
display: flex;
justify-content: space-between;
align-items: center;
>div{
    display: flex;
    p{
      width: 30px;
      height: 30px;
      background-color: rgba(15, 28, 63, 1);
      border: 1px solid rgba(32, 46, 83, 1);
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 100%;
      margin-left: 10px;
      cursor:pointer;
      img{
        display: block;
        width: 20px;
        height: 20px;
      }
    }
}

 @media (max-width: 768px) {
 >div{
 display:none;
 }
    
  }

`;

const StyledCards = styled.div`
  width: 1000px;
  @media (max-width: 768px) {
        width: 100%;
  }
`;


const StyledCardWrapper = styled.div`
  display: flex;
  position: relative;
    @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
    margin-right:0;
    margin-top: 20px;
  }
`;

const StyledTitle = styled.h4`
font-size: 18px;
font-weight: bold;
color: ${(props) => props.theme.color.whites[1001]};
margin: 25px 0;

`;

const StyledContent = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  box-sizing: border-box;
`;


export default FarmCards

