import React, {useContext} from 'react';
import Carousel from "nuka-carousel";
import {Context as FarmsContext} from "../../../contexts/Farms";
import styled from "styled-components";
import Slider, { Range } from 'rc-slider';

export const TestWidget: React.FC<{ slideIndex: number,setSlideIndex:any }> = ({slideIndex, children,setSlideIndex}) => {
    const {farms} = useContext(FarmsContext);
    return (

        <>

            <StyleCradsWap>
                {
                    document.querySelector('body').offsetWidth < 786 ? children : <Carousel className="Carousel" slidesToShow={4}
                                                                                            cellSpacing={10}
                                                                                            slidesToScroll={'auto'}
                                                                                            slideIndex={slideIndex}
                                                                                            defaultControlsConfig={{
                                                                                                nextButtonStyle: {display: 'none'},
                                                                                                prevButtonStyle: {display: 'none'},
                                                                                                pagingDotsStyle: {display: 'none'},
                                                                                            }}
                                                                                            afterSlide={setSlideIndex}
                                                                                            edgeEasing={'easeCubicIn'}
                    >
                        {children}
                    </Carousel>
                }

            </StyleCradsWap>


            {
                farms.length > 3 ?
                     <StyleSlider>
                <Slider min={0}
                value={slideIndex}
                defaultValue={0}
                max={farms.length -4}
                onChange={setSlideIndex}
                handleStyle={{width:"50px",borderRadius:"50px",border:"none"}}
                trackStyle={{display:"none"}}
                />
                </StyleSlider> :<></>

            }

        </>
    );
};


const StyleCradsWap = styled.div`
.children{
display: none;
}
 @media (max-width: 768px) {
        .children{
        display: block;
        }
        .Carousel{display: none;}
  }
`;

const StyleSlider = styled.div`
width: 100px;
margin: 20px auto;
 @media (max-width: 768px) {
        display: none;
  }
`;


